.main {
  max-width: 940px;
  width: 100%;
  margin: 0 auto;
  padding: 0;

  @media (min-width: 320px) and (max-width: 700px) {
    max-width: 320px;
    width: 100%;
  }
}

.naming-project {}

.naming-project__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  line-height: 21.83px;

  @media (min-width: 320px) and (max-width: 700px) {
    flex-direction: column;
    align-items: flex-start;
  }
}

.naming-project__title {
  font-size: 47.34px;
  display: block;
  flex-direction: column;
  text-transform: capitalize;
  line-height: 58.61px;
  width: 129px;

  @media (min-width: 320px) and (max-width: 700px) {
    margin: 65px 0 0 0;
  }
}

.naming-project__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.naming-project__text-ui {
  margin: 0;
  padding: 0;
  padding-top: 20%;
}

.naming-project__text-minsk {
  margin: 0;
  padding: 1px;
}

.naming-project__lang {
  color: #828282;
  font-size: 16px;
  transform: rotate(-90deg);
  text-transform: capitalize;
  height: 20px;

  @media screen and (max-width: 700px) {
    display: none;
  }
}

.naming-project__img-wrapper {
  max-width: 320px;
  width: 100%;
  height: 478px;
}

.naming-project__img {
  width: 100%;
  height: 100%;
}

.naming-project__img-mobile {
  width: 100%;
  min-width: 320px;

  @media (min-width: 700px) {
    display: none;
  }
}

.about-me {
  margin: 224px auto 0 auto;
  padding: 0;
  width: 425px;

  @media (min-width: 320px) and (max-width: 700px) {
    margin: 164px auto 0;
    width: 320px;
  }
}

.about-me__title {
  text-transform: capitalize;
  font-size: 34px;
  line-height: 42.09px;
}

.about-me__text-ui {

  line-height: 21.83px;
  font-size: 18px;
}

.about-me__text-courses {
  margin-top: 30px;
  line-height: 21.83px;
  font-size: 18px;
}

.about-me__text-implement {
  margin-top: 30px;
  line-height: 21.83px;
  font-size: 18px;
}

.about-me__br-mibile {
  @media (min-width: 700px) {
    display: none;
  }
}

