.contacts {
  margin-top: 200px;
  @media (min-width: 320px) and (max-width: 700px) {
    margin-top: 162px;
}
}

.contacts__title {
  line-height: 42.09px;
  font-size: 34px;
  text-transform: capitalize;
  margin: 0;
  padding: 0;
}

.contacts__text-welcome {
  color: black;
  margin-top: 20px;
  font-size: 18px;
  line-height: 21.83px;
}

.contacts__button-send {
  margin-top: 29px;
  background: #070707;
  max-width: 160px;
  max-height: 60px;
  width: 100%;
  height: 100%;
  height: auto;
  border-radius: 25%;
}

.contacts__text-button {
  font-size: 18px;
  align-items: center;
  line-height: 22px;
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 0;
}

.contacts__social-container {
  display: flex;
  flex-direction: row;
  max-width: 262px;
  width: 100%;
  padding: 0;
  justify-content: space-between;
  margin: 80px auto 45px auto;
}

.contacts__in-img {}

.contacts__inst-img {}

.contacts__be-img {}

.contacts__ball-img {}

.contacts__text-like-me-on {
  font-size: 14px;
  line-height: 17px;
  color: #828282;
}

.contacts__text-social-manuscript {
  margin-bottom: 63px;
  text-transform: capitalize;
  font-size: 14px;
  line-height: 17px;
  color: #828282;
}

.contacts__link-img {
  :hover {
    width: 60px;
    height: 60px;
    transition-duration: 0.5s;
    transition-delay: 0.1s;
    background-color: rgba(7, 255, 255, 0.315);
    border-radius: 60%;
  }
}

.contacts__link-img-in {
  :hover {
    border-radius: 15%;
  }
}

.contacts__link-img-inst {
  :hover {
    border-radius: 30%;
  }
}
